import React, { useState } from 'react';
import Modal from 'react-modal';
import styled from 'styled-components';
import "./Modal.css";
import { useMediaQuery } from 'react-responsive'


const ModalPage = (props) => {
  const isPhone = useMediaQuery({ maxWidth:600 })
  const isDesktop = useMediaQuery({ minWidth:601 })

  const imgUrl = "/images/Event.png"
  console.log(props.modalOff);
  const [modal, setModal] = React.useState(true); // 모달창

  const modalOff = () => {
    setModal(false);
  };
  
  return (
    <>
      {isPhone &&
        <div>
        <Modal
          isOpen={modal}
          ariaHideApp={true}
          onRequestClose={modalOff}
          style={{
            overlay: {
              position: 'fixed',
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              backgroundColor: 'rgba(255, 255, 255, 0.75)'
            },
            content: {
              position: 'absolute',
              top: '25%',
              left: '15%',
              right: '15%',
              bottom: '25%',
              border: '1px solid #ccc',
              background: 'white',
              overflow: 'auto',
              WebkitOverflowScrolling: 'touch',
              borderRadius: '8px',
              outline: 'none',
              padding: '20px'
            }
          }}
        >
            <div style={{ height:"100%" }}>
                <img style={{ width:"100%", height:"95%", marginTop:"5%" }} alt="iPhone_01" src="images/Event.png" />
                {/* <div style={{ display:"flex", justifyContent:"center" }}>
                    <button onClick={modalOff} style={{ backgroundColor:"aqua" }}>Close</button>
                </div> */}
            </div>
        </Modal>
      </div>
      }
      {isDesktop &&
        <div>
          <div>
        <Modal
          isOpen={modal}
          ariaHideApp={true}
          onRequestClose={modalOff}
          style={{
            overlay: {
              position: 'fixed',
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              backgroundColor: 'rgba(255, 255, 255, 0.75)'
            },
            content: {
              position: 'absolute',
              top: '7%',
              left: '28%',
              right: '28%',
              bottom: '7%',
              border: '1px solid #ccc',
              background: 'white',
              overflow: 'auto',
              WebkitOverflowScrolling: 'touch',
              borderRadius: '8px',
              outline: 'none',
              padding: '20px'
            }
          }}
        >
            <div style={{ height:"100%" }}>
                <img style={{ width:"100%", height:"95%", marginTop:"5%" }} alt="iPhone_01" src="images/Event.png" />
                {/* <div style={{ display:"flex", justifyContent:"center" }}>
                    <button onClick={modalOff} style={{ backgroundColor:"aqua" }}>Close</button>
                </div> */}
            </div>
        </Modal>
      </div>
        </div>
      }
      <div></div>
      {/* <div>
        <Modal
          isOpen={modal}
          ariaHideApp={true}
          onRequestClose={modalOff}
          style={{
            overlay: {
              position: 'fixed',
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              backgroundColor: 'rgba(255, 255, 255, 0.75)'
            },
            content: {
              position: 'absolute',
              top: '20%',
              left: '15%',
              right: '15%',
              bottom: '20%',
              border: '1px solid #ccc',
              background: 'white',
              overflow: 'auto',
              WebkitOverflowScrolling: 'touch',
              borderRadius: '8px',
              outline: 'none',
              padding: '20px'
            }
          }}
        >
            <div style={{ height:"100%" }}>
                <img style={{ width:"100%", height:"95%", marginTop:"5%" }} alt="iPhone_01" src="images/Event.png" />
                {/* <div style={{ display:"flex", justifyContent:"center" }}>
                    <button onClick={modalOff} style={{ backgroundColor:"aqua" }}>Close</button>
                </div> 
            </div>
        </Modal>
      </div> */}
    </>
  );
};
  
  export default ModalPage;